import { setup } from "js/app";
import "sasspages/projects";

setup(); // sets up core site functionality 

document.addEventListener('DOMContentLoaded', function () {

    const $ = document.querySelector.bind(document);
    const $$ = document.querySelectorAll.bind(document);

    setTimeout(() => {
        let icon = $(".projects").querySelector(".fa-envelope");
        icon.classList.remove("animated", "bounceInDown");
        icon.classList.add("animated", "pulse");
    }, 3000);

});

